.secondSection {
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: black; /* 검은 배경 */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.secondSectionText {
    font-size: 4rem;
    color: rgba(255, 255, 255, 0); /* 텍스트 초기 상태는 투명 */
    animation: textFadeIn 7s ease forwards; /* 텍스트 페이드인 애니메이션 */
}

@keyframes textFadeIn {
    0% {
        color: rgba(255, 255, 255, 0);
        text-shadow: 0 0 10px rgba(255, 0, 0, 0.8); /* 빨간 빛 퍼짐 효과 */
    }
    100% {
        color: rgba(255, 255, 255, 1); /* 텍스트가 완전히 나타남 */
        text-shadow: 0 0 30px rgba(255, 0, 0, 0.8),
        0 0 60px rgba(255, 153, 0, 0.8),
        0 0 90px rgba(255, 255, 0, 0.8); /* 텍스트 주위 빛 효과 */
    }
}